<script>
  import Moment from "moment";
  export default {
    name: "Confirmation",
    components: {},
    props: {
      userName: String,
      cancelStatus: Boolean,
      eventData: Object,
      response: Object,
    },
    data() {
      return {
        additional_info: {},
      };
    },
    created() {
      this.filterCustomfieldData();
    },
    methods: {
      reloadPage() {
        this.$router.go();
      },
      downloadICS() {
        window.open("data:text/calendar;charset=utf8," + escape(this.response.ical_data));
      },
      timeformat(time, booking_time, format) {
        if (!booking_time) return Moment(time).format("llll");
        return Moment.utc(Moment(time * 1000), "YYYY-MM-DD HH")
          .tz(this.eventData.time_zone)
          .format(format);
      },
      filterCustomfieldData() {
        let form = JSON.parse(this.eventData.events_participants.custom_fields);
        Object.keys(form).forEach((key) => {
          if (key != "name" && key != "email" && key != "phone") this.additional_info[key] = form[key];
        });
      },
    },
  };
</script>

