import Data from "@/js/core/data";

const Schedulely_Server = "https://api.${region}.500apps.com/schedulely/v1/";
const Shortcut_Server = "https://api.500apps.com/shortcuts/shortcut";
const Shortcut_Region_Server = "https://api.${region}.500apps.com/shortcuts/shortcut";

class Schedulely {
  // Get Data by Params
  get(params) {
    // eslint-disable-next-line no-unused-vars
    let region = window.region;
    let url = eval("`" + ` ${Schedulely_Server + params}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }

  // Add Data
  add(params, form) {
    // eslint-disable-next-line no-unused-vars
    let region = window.region;
    let url = eval("`" + ` ${Schedulely_Server + params}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.multi_call(form);
  }

  // Edit Data
  edit(id, params, form) {
    // eslint-disable-next-line no-unused-vars
    let region = window.region;
    let url = eval("`" + ` ${Schedulely_Server + params}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.edit(id, form);
  }
  get_agents(region, params) {
    let url = "";
    url = eval("`" + ` ${Schedulely_Server + params}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
  async getShortcutData(name) {
    let url;
    if (window.region && (window.region == "dev" || window.region == "qa")) url = eval("`" + ` ${Shortcut_Region_Server}/${name}` + "`");
    else url = eval("`" + ` ${Shortcut_Server}/${name}` + "`");
    let data = new Data(url);
    data.disablePCors();
    return data.get();
  }
}

export default new Schedulely();
